import React from 'react'
import { Layout } from '../components/Layout'
import { css } from 'emotion'
import { graphql } from 'gatsby'
import { TagLink } from '../components/TagLink';

export const styles = {}
styles.container = css`
  p {
    font-size: 18px;
  }
  h3 {
    font-size: 32px;
  }
  margin-bottom: 20px;
`

/**
 * About page component.
 */
export default ({ data: { file } }) => (
  <Layout title="About" active="about" image={file.image.fluid}>
    <div className={styles.container}>
      <i>TODO: Yeah, definitely I have to improve this page...</i>
      <p>This blog contains updates of any of my current primary interests.</p>
      <p>
        Those are: <TagLink tag="gamedev">Game development</TagLink>,&nbsp;
        <TagLink tag="programming">programming</TagLink>,&nbsp;
        <TagLink tag="art">digital art</TagLink>,&nbsp;
        <TagLink tag="diy">DIY electronics</TagLink>&nbsp;
        among others.
      </p>
      <h3>About me</h3>
      <p>
        I have few years of programming experience. I've been leader of some
        projects as full stack developer, and I have years of experimenting in
        the field at personal level. The technologies that make me feel more
        comfortable at this moment are NodeJS, React and the space surrounding
        them. Also I've worked for some time as Java Developer, and personally I
        enjoyed in the past programming JVM related languages (Kotlin, Clj and
        related).
        Despite of that, almost no content is visible, and there is where this
        blog kicks in!
      </p>
      <p>
        Be a back-end developer is cool, but lately I'm staying at the front
        side, because I find it less painful (If you have a good designer by
        your side, of course!). But to be honest, I'm a little bit burnt-out of
        the programming world (professionally), so I'm trying to bring my
        personal career to an artistic plane. I still enjoy programming the most
        tho!
      </p>
      <h3>Who's Esmer?</h3>
      <p>
        <i>Esmer</i> the name of a fictional character from a game that I'm
        planning to develop in a near future. Also the logo of this blog is
        related to that.
      </p>
      <h3>Artistic material posted</h3>
      <p>
        About the material posted, while most of them might be practice
        material, some others might contain drafts or previews from concepts for
        my future games.
      </p>
      <p>
        This means that, unless stated, the material should be shared under the
        same terms of the web page. This includes also images or other material
        outside of a post (Like the header or the logo of this blog).
      </p>
      <p>
        If you want to use some of my material outside (Eg. reddit) and you're
        not sure of something, feel free to ask me (In the footer, go to
        "Contact me").
      </p>
      <p>
        As a final line about this topic, I want to say that I don't want anyone
        to feel discouraged to share, rather the opposite! I would feel very
        comforted if I knew that someone, somewhere enjoys what I do and shares
        it and spreads it over the internet!
      </p>
    </div>
  </Layout>
)

export const query = graphql`
  query AboutPageAssets {
    file(relativePath: { eq: "about-header.jpg" }) {
      image: childImageSharp {
        fluid(maxWidth: 2048) {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`
